import React from 'react';
import { connect } from 'react-redux';
import {
  notificationsSelector,
  openNotification,
  closeNotification
} from '../../store/notifications';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import CheckCircleIcon from '@material-ui/icons/CheckCircle';
import ErrorIcon from '@material-ui/icons/Error';
import InfoIcon from '@material-ui/icons/Info';
import CloseIcon from '@material-ui/icons/Close';
import { amber, green } from '@material-ui/core/colors';
import IconButton from '@material-ui/core/IconButton';
import Snackbar from '@material-ui/core/Snackbar';
import SnackbarContent from '@material-ui/core/SnackbarContent';
import WarningIcon from '@material-ui/icons/Warning';
import { makeStyles } from '@material-ui/core/styles';

const variantIcon = {
  success: CheckCircleIcon,
  warning: WarningIcon,
  error: ErrorIcon,
  info: InfoIcon
};

const useStyles1 = makeStyles(theme => ({
  success: {
    backgroundColor: green[600]
  },
  error: {
    backgroundColor: theme.palette.error.dark
  },
  info: {
    backgroundColor: theme.palette.primary.main
  },
  warning: {
    backgroundColor: amber[700]
  },
  icon: {
    fontSize: 20
  },
  iconVariant: {
    opacity: 0.9,
    marginRight: theme.spacing(1)
  },
  message: {
    display: 'flex',
    alignItems: 'center'
  }
}));

function MySnackbarContentWrapper(props) {
  const classes = useStyles1();
  const { className, message, onClose, variant, ...other } = props;
  const Icon = variantIcon[variant];

  return (
    <SnackbarContent
      className={clsx(classes[variant], className)}
      aria-describedby="client-snackbar"
      message={
        <span id="client-snackbar" className={classes.message}>
          <Icon className={clsx(classes.icon, classes.iconVariant)} />
          {message}
        </span>
      }
      action={[
        <IconButton
          key="close"
          aria-label="close"
          color="inherit"
          onClick={onClose}
        >
          <CloseIcon className={classes.icon} />
        </IconButton>
      ]}
      {...other}
    />
  );
}

MySnackbarContentWrapper.propTypes = {
  className: PropTypes.string,
  message: PropTypes.string,
  onClose: PropTypes.func,
  variant: PropTypes.oneOf(['error', 'info', 'success', 'warning']).isRequired
};

export default WrappedComponent => {
  const withNotifications = ({
    openNotification,
    closeNotification,
    notifications,
    ...other
  }) => {
    const handleClose = notificationId => (e, reason) => {
      if (reason === 'clickaway') {
        return;
      }
      closeNotification(notificationId);
    };
    const currentNotification = notifications.find(
      notification => !!notification.isActive
    );
    return (
      <>
        <WrappedComponent {...other} openNotification={openNotification} />
        {currentNotification && (
          <Snackbar
            anchorOrigin={{
              vertical: 'bottom',
              horizontal: 'right'
            }}
            open
            autoHideDuration={2500}
            onClose={handleClose(currentNotification.id)}
          >
            <MySnackbarContentWrapper
              onClose={handleClose(currentNotification.id)}
              variant={currentNotification.variant}
              message={currentNotification.text}
            />
          </Snackbar>
        )}
      </>
    );
  };

  const mapStateToProps = state => ({
    notifications: notificationsSelector(state)
  });
  const mapDispatchToProps = {
    openNotification,
    closeNotification
  };
  return connect(
    mapStateToProps,
    mapDispatchToProps
  )(withNotifications);
};
